export default {
	name: 'TabBar',
	data() {
		return {
			currentHash:'',
		}
	},
	methods: {
	},
	
	mounted() {
		var hash=window.location.hash;
		this.currentHash=hash.substr(1,hash.length);
	},
	created(){
		
	},
	
}



